export default {
  name: "LayoutDefault",

  data() {
    return {
      leftDrawerOpen: false,
      navigationItems: [
        {
          icon: "school",
          label: "Home",
          caption: "Cards List",
          toRouteName: "Home"
        },
        {
          icon: "code",
          label: "AddCard",
          caption: "Add more cards",
          toRouteName: "AddCard"
        },
        {
          icon: "chat",
          label: "Tags",
          caption: "Manage your tags",
          toRouteName: "Tags"
        }
      ]
    };
  },
  methods: {
    navigateTo(routeName) {
      if (this.$route.name !== routeName)
        this.$router.push({ name: routeName });
      else {
        this.leftDrawerOpen = false;
      }
    }
  }
};
