import store from "@/store";

export default {
  name: "Home",
  async beforeRouteEnter(routeTo, routeFrom, next) {
    await Promise.all([
      store.dispatch("fetchTags"),
      store.dispatch("fetchCards")
    ]);
    next();
  },
  data() {
    return {
      currentIndex: 0,
      isFrontSide: true,
      isTransitioning: false
    };
  },
  computed: {
    currentCard() {
      return this.cardsArray[this.currentIndex];
    },
    cardsArray() {
      return this.$store.getters.cardsArray;
    },
    tags() {
      return this.$store.state.tags;
    }
  },
  methods: {
    handleSwipe({ direction }) {
      this.isTransitioning = true;
      const vm = this;
      setTimeout(function() {
        vm.isFrontSide = true;
        if (direction === "rigth") {
          vm.currentIndex = vm.currentIndex
            ? vm.cardsArray.length - 1
            : vm.currentIndex - 1;
        } else {
          vm.currentIndex = (vm.currentIndex + 1) % vm.cardsArray.length;
        }
        vm.isTransitioning = false;
      }, 300);
    }
  }
};
